@import "../mixin/baseImport";
@import "../_sprite/common_sprite";


/**
 * 팝업 공통
 */
.modal {position: fixed;top: 0;left: 0;min-width: 1024px;width: 100%;height: 100%;z-index: $z-layer1;
	&__outer{display: block;width: 100%;height: 100%;}
	&__inner{height: 100%;vertical-align: middle; position: relative;}
	&__dim {position: absolute;top: 0;left: 0;width: 100%;height: 100%;background-color: rgba(16,41,54,0.85);}
	&__content{box-sizing: border-box;}
	.modal__content{width: 1024px;height: 768px;margin: 0 auto;display: flex;align-items: center;justify-content: center}
}

.modal-content {position: fixed;top: 0;left: 0;min-width: 1024px;width: 100%;height: 100%;z-index: $z-layer1;
	&__outer{display: block;width: 100%;height: 100%;}
	&__inner{vertical-align: middle;height: 100%; position: relative;}
	&__dim {position: absolute;top: 0;left: 0;width: 100%;height: 100%;background-color: rgba(35,33,31,0.7);}
	.modal__content{width: 1024px;height: 768px;margin: 0 auto;display: flex;align-items: center;justify-content: center}
}