fieldset,img {border:0 none; vertical-align:top;}
img{image-rendering: -webkit-optimize-contrast;}
blockquote, q {quotes:none}
blockquote:before, blockquote:after,q:before, q:after {content:'';content:none}
//input,select,textarea,button {font-size:100%;vertical-align:middle}

table {width:100%; text-align:left;}
button{padding: 0;
	&:focus,
	&:active{ padding:0;}
}

input[type="button"], input[type="submit"], input[type="reset"], input[type="file"]::-webkit-file-upload-button, button{ padding:0; font-family: $notoecp;appearance:none;}
a{text-decoration:none;cursor:pointer; color:$base-font-color;
	&:link,
	&:visited,
	&:hover,
	&:active{text-decoration:none;}
}

caption{overflow: hidden;width: 1px;height: 1px;font-size: 20px;line-height: 1px; margin-top:-1px;}

address,caption,cite,code,dfn,em{}

select,input,button,textarea{appearance:none;border-radius: 0;background-color:transparent;border:0 none;font-size:$base-font-size; font-weight:$font-n; font-family: $notoecp;
//	@common placeholder()
	&[type="checkbox"]{ -webkit-appearance:checkbox; }
	&[type="radio"]{ -webkit-appearance:radio;}
	&::-ms-expand {display: none;}
}
textarea{overflow-y: scroll; -webkit-overflow-scrolling: touch;}


html,body{height:100%;font-family:$base-font; font-size:$base-font-size; font-weight:$font-n; color:$txt-normal;}

//body{overflow:hidden;}

strong,h1,h2,h3,h4,h5,h6{font-weight: $font-r;}

label,button{ cursor:pointer; overflow:hidden; outline:none}

a,input{outline:none;padding: 0;}

a,button,label,input,textarea{-webkit-tap-highlight-color: rgba(0,0,0,0);}