@import "../../mixin/baseImport";

//cclub house hover
@mixin btn-clubhouse($target) {
	.clubhouse{
		.btn{
			&:#{$target}{
				.icon{
					&.icon-clubhouse{
						&:before{opacity: 1 !important;}
					}
				}
			}
		}
	}
}
//main hover
@mixin btn-week-mission($target){
	.week-mission{
		.btn{
			&:#{$target}{
				.icon{
					@for $i from 1 through 6 {
						&.icon-btn0#{$i}{
							&.default{background-image: url('/assets/images/main/week/icon_week_icon0#{$i}_active.png') !important;}
						}
					}
				}
			}
		}
	}
}
//week tree hover
@mixin week-tree-btn-hover($target) {
	.month__monster{
		.btn{
			&-intro:#{$target}{transform: scale(1.05) !important}
		}
	}
	.month__item-week{
		.btn{
			&-week:#{$target}{transform: translate3d(0,-2px,0) scale(1.02) !important}
		}
	}
	.movie-play{
		.btn{
			&-intro,
			&-epilogue{
				&:#{$target}{
					&:before{opacity: 1 !important;}
				}
			}
		}
	}
}
//vocabulary list hover
@mixin with-parents-vl-btn-select($target) {
	.select-box{
		&__layer{
			.btn{
				&-select{
					&:#{$target}{
						&:not(.disabled) {
							span{color: #2d4ef1 !important;}
						}
					}
				}
			}
		}
	}
}
//vocabulary list popup hover
@mixin with-parents-vl-popup-btn-hover($target) {
	.controller{
		.btn{
			&-print{
				&:#{$target}{transform: scale(1.05) !important;}
			}
		}
	}
	.vocabulary-card__items{
		.btn{
			&-close{
				&:#{$target}{background-color: #b7ede8 !important;}
			}
		}
	}
}
//플래시 카드 hover
@mixin flashcard-popup-btn-hover($target) {
	.main-flashcard01{
		&__header{
			.btn{
				&{
					&-help{
						&:#{$target}{background-color: #eeefec;}
					}
					&-close{
						&:#{$target}{background-color: #eeefec;}
					}
				}
			}
		}
	}
	.main-flashcard-detail01{
		.icon{
			&-close{
				&:#{$target}{background-color: #4cc8cb;}
			}
		}
	}
}

//gnb hover
@mixin header-btn-hover($target) {
	.hl-group{
		&__item{
			.btn{
				$list: week weekmap home back;
				@each $author in $list {
					.icon-#{$author}{
						&:#{$target}{background-image: url("/assets/images/common/header/hl_group/btn_#{$author}01_active.png")}
					}
				}
			}
		}
	}
	.hr-group{
		.btn-menu{
			&:#{$target}{background-image: url("/assets/images/common/header/hr_group/btn_gnb_menu01_active.png")}
		}
		&__item{
			.btn{
				$list: profile bgm bgm_mute help logout parent;
				@each $author in $list {
					.icon-#{$author}{
						&:#{$target}{background-image: url("/assets/images/common/header/hr_group/btn_#{$author}01_active.png")}
					}
				}
				$list: kor eng;
				@each $author in $list {
					.icon-#{$author}{
						&:#{$target}{background-image: url("/assets/images/common/header/hr_group/btn_#{$author}01_active.png")}
					}
				}
			}
		}
	}
}

//alert hover
@mixin alert-btn-hover($target) {
	.main-alert01{
		.btn{
			&-close{
				&:#{$target}{background-color: rgba(199,132,69,0.3);}
			}
		}
	}
}

//profile hover
@mixin profile-btn-hover($target) {
	.main-profile01{
		.user-info{
			&-box{
				.btn-box{
					.btn{
						&:#{$target}{transform: scale(1.05) !important;}
					}
				}
			}
		}
		.btn{
			&-close{
				&:#{$target}{background-color: #ffd872 !important;}
			}
		}
		.icon{
			$iconList : yuri, oscar, pepper, leo;
			@each $item in $iconList {
				&-#{$item}{
					&:#{$target}{background-image: url("/assets/images/main/popup/profile/icon_#{$item}01_active.png") !important;}
				}
			}
		}
	}
}

//alert hover
@mixin club-song-btn-hover($target) {
	.sing-along01{
		.btn{
			&-close{
				&:#{$target}{background-color: rgba(49, 40, 40, 0.1) !important;}
			}
		}
	}
}

#wrap{
	&.mo{
		#gnb{
			@include header-btn-hover('active');
		}
		&.main{
			@include btn-clubhouse('active');
			@include btn-week-mission('active');
		}
		&.week-tree{
			@include week-tree-btn-hover('active');
		}
		&.with-parents-vocabulary-lists{
			@include with-parents-vl-btn-select('active');
		}
		@include profile-btn-hover('active');
		@include flashcard-popup-btn-hover('active');
		@include with-parents-vl-popup-btn-hover('active');
		@include alert-btn-hover('active');
		@include club-song-btn-hover('active');
	}
	&.pc{
		#gnb{
			@include header-btn-hover('hover');
		}
		&.main{
			@include btn-clubhouse('hover');
			@include btn-week-mission('hover');
		}
		&.week-tree{
			@include week-tree-btn-hover('hover');
		}
		&.with-parents-vocabulary-lists{
			@include with-parents-vl-btn-select('hover');
		}
		@include profile-btn-hover('hover');
		@include flashcard-popup-btn-hover('hover');
		@include with-parents-vl-popup-btn-hover('hover');
		@include alert-btn-hover('hover');
		@include club-song-btn-hover('hover');
	}
}
